<template>
  <div class="seting-box">
    <s-header :name="'Account management'"></s-header>
    <div class="input-item">
      <van-field v-model="nickName" label="nickName" />
      <van-field v-model="introduceSign" label="Signature" />
      <van-field v-model="password" type='password' label="Password" />
      <van-field v-model="upUserId" type='text' label="UpUserKey" />
    </div>
    <van-button class="save-btn" color="#1baeae" type="primary" @click="save" block>Save</van-button>
    <van-button class="save-btn" color="#1baeae" type="primary" @click="logout" block>Log Out</van-button>
  </div>
</template>

<script>
import sHeader from '@/components/SimpleHeader'
import { getUserInfo, EditUserInfo, logout } from '../service/user'
import { setLocal } from '@/common/js/utils'
import { getLocal } from '../common/js/utils'
import { Toast } from 'vant'
export default {
  components: {
    sHeader
  },
  data() {
    return {
      nickName: '',
      introduceSign: '',
      password: '',
      upUserId:''
    }
  },
  async mounted() {
   
    // console.log(upUserId);
    const { data } = await getUserInfo();
       let upUserId =   getLocal("upUserId") ||"";
       this.upUserId =  upUserId;
    this.nickName = data.nickName;
    this.introduceSign = data.introduceSign;
  },
  methods: {
    async save() {
      const params = {
        introduceSign: this.introduceSign,
        nickName: this.nickName,
        passwordMd5: this.$md5(this.password),
        upUserId:this.upUserId
      }
      // const { data } = await
       EditUserInfo(params).then(res=>{
      //  console.log(res);
      setLocal("username",this.nickName);
      setLocal("upUserId",this.upUserId);
      Toast.success('Save Success')
       })
      
    },
    async logout() {
      const { resultCode } = await logout()
      if (resultCode == 200) {
        setLocal('token', '')
        window.location.href = '/'
      }
    }
  }
}
</script>

<style lang="less" scoped>
  .seting-box {
    .input-item {
      margin-top: 44px;
    }
    .save-btn {
      width: 80%;
      margin: 20px auto ;
    }
  }
</style>
