<template>
  <div class="user-box">
    <s-header :name="'My'"></s-header>
    <div class="user-info">
      <div class="info">
        <img
          src="https://gimg2.baidu.com/image_search/src=http%3A%2F%2Fnimg.ws.126.net%2F%3Furl%3Dhttp%3A%2F%2Fdingyue.ws.126.net%2F2021%2F0407%2F89c83f67j00qr626t000kc000fj00adm.jpg%26thumbnail%3D650x2147483647%26quality%3D80%26type%3Djpg&refer=http%3A%2F%2Fnimg.ws.126.net&app=2002&size=f9999,10000&q=a80&n=0&g=0n&fmt=jpeg?sec=1636273576&t=d8ff0582c550b560803881bc48840ac2"
        />

        <!-- <div class="user-desc">
          <span>昵称：{{ user.nickName }}</span>
          <span>登录名：{{ user.loginName }}</span>
          <span class="name">个性签名：{{ user.introduceSign }}</span>
        </div>-->
      </div>
      <div class="info-level">
        <div class="level-item" v-for="(item, index) in levels" :key="index">
          <div @click="goBank(item.type)" class="item-top item-col">{{item.content}}</div>
          <div class="item-bottom item-col">{{item.letter}}</div>
        </div>
      </div>
    </div>
    <ul class="user-list">
      <li @click="goTo('goldorder')">
        <i class="iconfont icon-emaxcitygerenxinxitubiaoji03 blue"></i>
        <span>Smart buy order</span>
        <van-icon name="arrow" />
      </li>
      <li @click="goTo('order')">
        <i class="iconfont icon-my-order-copy blue"></i>
        <span>My order</span>
        <van-icon name="arrow" />
      </li>
      <li @click="goTo('wallet')">
        <i class="iconfont icon-mywallet blue"></i>
        <span>My wallet</span>
        <van-icon name="arrow" />
      </li>
      <li @click="goTo('setting')">
        <i class="iconfont icon-icon41-copy yellow"></i>
        <span>Coupon center</span>
        <van-icon name="arrow" />
      </li>
      <!-- <li @click="goTo('bank')">
        <i class="iconfont icon-my_bank yellow"></i>
        <span>My bank</span>
        <van-icon name="arrow" />
      </li>-->
      <li @click="goTo('address?from=mine')">
        <i class="iconfont icon-MemberCenter blue"></i>
        <span>Member center</span>
        <van-icon name="arrow" />
      </li>
      <li @click="goTo('about')">
        <i class="iconfont icon-my_comment green"></i>
        <span>My comment</span>
        <van-icon name="arrow" />
      </li>

      <li @click="goTo('about')">
        <i class="iconfont icon-Account-security-copy-copy blue"></i>
        <span>Account security</span>
        <van-icon name="arrow" />
      </li>
      <li @click="goTo('about')">
        <i class="iconfont icon-38-Actionparametersparamspreferencessett green"></i>
        <span>Parameter settings</span>
        <van-icon name="arrow" />
      </li>
      <li @click="goTo('about')">
        <i class="iconfont icon-emaxcitygerenxinxitubiaoji03 pupr"></i>
        <span>Return/Refund history</span>
        <van-icon name="arrow" />
      </li>
    </ul>
    <nav-bar></nav-bar>
  </div>
</template>

<script>
import navBar from "@/components/NavBar";
import sHeader from "@/components/SimpleHeader";
import { getUserInfo } from "../service/user";
import { getRest } from "../service/golddetail.js";
import { Toast } from "vant";
import { getLocal } from "../common/js/utils";
export default {
  // inject: ['reload'],
  components: {
    navBar,
    sHeader
  },
  data() {
    return {
      rest: 0,
      userName: "",
      value: true,
      user: {},
      levels: [
        {
          content: 0,
          letter: "Available Balance",
          type: "wallet"
        },
        {
          content: "",
          letter: "UserName"
        },
        {
          content: "vip0",
          letter: "Membership level"
        }
      ]
    };
  },

  mounted() {
    Toast.loading({
      message: "Loading...",
      forbidClick: true
    });
    let username = getLocal("username") || "";
    this.levels[1].content = username;
    //  console.log(username);
    getUserInfo().then(res => {
      this.user = res.data;
    });
    // this.user = data;
    getRest().then(res => {
      // console.log("res",res);
      this.rest = res.data.rest;
      this.levels[0].content = this.rest;
      if (res.data.sumIn >= 0 && res.data.sumIn < 50000) {
        this.levels[2].content = "Normal VIP";
      } else if (res.data.sumIn >= 50000 && res.data.sumIn < 100000) {
        this.levels[2].content = "VIP1";
      } else if (res.data.sumIn >= 100000 && res.data.sumIn < 200000) {
        this.levels[2].content = "VIP2";
      } else if (res.data.sumIn >= 200000 && res.data.sumIn < 500000) {
        this.levels[2].content = "VIP3";
      } else {
        this.levels[2].content = "VIP4";
      }
      Toast.clear();
    });
  },
  methods: {
    //   reload() {
    //   this.isReload = false
    //   this.$nextTick(() => {
    //     this.isReload = true
    //   })
    // },
    goBank(type) {
      this.$router.push({ path: type });
    },
    goBack() {
      this.$router.go(-1);
    },
    goTo(r) {
      if(r=="address?from=mine"){
        Toast.fail("Not yet open")
        return ;
      }
      this.$router.push({ path: r });
    }
  }
};
</script>

<style lang="less" scoped>
@import "../common/style/mixin";
.user-box {
  .user-header {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10000;
    .fj();
    .wh(100%, 44px);
    line-height: 44px;
    padding: 0 10px;
    .boxSizing();
    color: #252525;
    background: #fff;
    border-bottom: 1px solid #dcdcdc;
    .user-name {
      font-size: 14px;
    }
  }
  .user-info {
    // width: 94%;
    // margin: 10px;
    position: relative;
    height: 200px;
    // background: linear-gradient(90deg, @primary, #51c7c7);
    background-color: #f6e05c; //#15337b;
    box-shadow: 0 2px 5px #c5d11b;
    // border-radius: 6px;
    margin-top: 45px;
    .info-level {
      width: 100%;
      height: 60px;
      color: #fff;
      background-color: rgba(209, 128, 23, 0.6);
      position: absolute;
      bottom: 0;
      justify-content: space-between;
      display: flex;
      text-align: center;
      .level-item {
        width: 33.33%;
        display: flex;
        flex-direction: column;
        .item-col {
          flex: 1;
        }
        .item-top {
          font-weight: 700;
          font-size: 14px;
          padding: 10px 0;
        }
        .item-bottom {
          font-weight: 200;
          font-size: 10px;
          padding-bottom: 5px;
        }
      }
    }
    .info {
      position: relative;
      display: flex;
      width: 100%;
      height: 100%;
      padding: 25px 20px;
      flex-direction: column;
      .boxSizing();
      img {
        margin: auto;
        .wh(60px, 60px);
        border-radius: 50%;
        margin-top: 4px;
      }
      .name {
      }

      .user-desc {
        display: flex;
        flex-direction: column;
        margin-left: 10px;
        line-height: 20px;
        font-size: 14px;
        color: #fff;
        span {
          color: #fff;
          font-size: 14px;
          padding: 2px 0;
        }
      }
      .account-setting {
        position: absolute;
        top: 10px;
        right: 20px;
        font-size: 13px;
        color: #fff;
        .van-icon-setting-o {
          font-size: 16px;
          vertical-align: -3px;
          margin-right: 4px;
        }
      }
    }
  }
  .user-list {
    padding: 20px;
    padding-bottom: 50px;
    margin-top: 10px;
    li {
      height: 40px;
      line-height: 40px;
      border-bottom: 1px solid #e9e9e9;
      display: flex;
      // justify-content: space-between;
      font-size: 14px;
      position: relative;
      i {
        line-height: 40px;
        font-size: 18px;
      }
      .blue {
        color: #00ff;
      }
      .yellow {
        color: #d1a76b;
      }
      .green {
        color: #71a166;
      }
      .pupr {
        color: #3c256b;
      }

      span {
        display: inline-block;
        margin-left: 8px;
      }
      .van-icon-arrow {
        position: absolute;
        right: 5px;
        line-height: 40px;
      }
    }
  }
}
</style>
