<template>
  <div class="categray">
    <div>
      <header class="category-header wrap">
        <i class="nbicon nbfanhui" @click="goHome"></i>
        <div class="header-search">
          <i class="nbicon nbSearch"></i>
          <router-link tag="span" class="search-title" to="./product-list?from=category">
            search&nbsp;&nbsp;&nbsp;&nbsp;
            &nbsp;&nbsp;&nbsp;
          </router-link>
        </div>
        <i class="iconfont icon-More"></i>
      </header>
      <nav-bar></nav-bar>
      <div class="search-wrap">
        <!-- ref="searchWrap" -->
        <div class="line" id="chartLineBox" style="width:100%;height: 40vh;"></div>
        <div class="line" id="chartLineBoxSliver" style="width: 100%;height: 40vh;"></div>
        <!-- <ve-line :option="option"></ve-line>  -->
        <!-- <list-scroll :scroll-data="categoryData" class="nav-side-wrapper">
          <ul class="nav-side">
            <li
              v-for="item in categoryData"
              :key="item.categoryId"
              v-text="item.categoryName"
              :class="{'active' : currentIndex == item.categoryId}"
              @click="selectMenu(item.categoryId)"
            ></li>
          </ul>
        </list-scroll>-->
        <!-- <div class="search-content"> -->
        <!-- <list-scroll :scroll-data="categoryData" > -->
        <!-- <div class="swiper-container">
              <div class="swiper-wrapper">
                <template v-for="(category, index) in categoryData">
                  <div class="swiper-slide" v-if="currentIndex == category.categoryId" :key="index">
                    <div class="category-list" v-for="(products, index) in category.secondLevelCategoryVOS" :key="index">
                      <p class="catogory-title">{{products.categoryName}}</p>
                      <div class="product-item" v-for="(product, index) in products.thirdLevelCategoryVOS" :key="index" @click="selectProduct(product)">
                        <img src="//s.weituibao.com/1583591077131/%E5%88%86%E7%B1%BB.png" class="product-img"/>
                        <p v-text="product.categoryName" class="product-title"></p>
                      </div>
                    </div>
                  </div>
                </template>
              </div>
        </div>-->
        <!-- </list-scroll> -->
        <!-- </div> -->
      </div>
    </div>
  </div>
</template>

<script>
import echarts from "echarts";
import navBar from "@/components/NavBar";
//  import listScroll from '@/components/ListScroll'
import { getCategory } from "../service/good";
import { Toast } from 'vant'
import { getGold, getgold, getAllAuAg } from "../service/golddetail";
export default {
  components: {
    navBar
    // listScroll
  },
  mounted() {
    Toast.loading({ message: "加载中...", forbidClick: true });
    getAllAuAg().then(res => {
      // console.log(res);
      let list = res.data;
      let goldList = [];
      let goldTimeList = [];
      let silverList = [];
      let silverTimeList = [];
      list.forEach(item => {
        if (item.isGold == 1 && item.lastPrice != 0) {
          // console.log(item);
          let golddate = item.timestamp.replace(/\-/g, "/");
          // console.log(golddate);
          var date = new Date(golddate);
          // console.log(date);
          let time1 = 60 * 60 * 1000 * 2.5;
          let time = item.timestamp;
          let t = date.getTime();
          t = t - time1; //获取印度时间时间戳
          this.getTime(t);
          goldList.push(item.lastPrice);
          goldTimeList.push(this.gettime);
          if (goldList.length % 300 == 0) {
            this.chartLine = echarts.init(
              document.getElementById("chartLineBox")
            );
            this.option.xAxis.data = goldTimeList;
            this.option.series[0].data = goldList;
            this.chartLine.setOption(this.option);
          }
        } else if (item.isGold == 0 && item.lastPrice != 0) {
          let silverdate = item.timestamp.replace(/\-/g, "/");
          var date = new Date(silverdate);
          let time1 = 60 * 60 * 1000 * 2.5;
          let time = item.timestamp;
          let t = date.getTime();
          t = t - time1; //获取印度时间时间戳
          this.getTime(t);
          silverList.push(item.lastPrice);
          silverTimeList.push(this.gettime);
          if (silverList.length % 300 == 0) {
            this.chartLine = echarts.init(
              document.getElementById("chartLineBoxSliver")
            );
            this.options.xAxis.data = silverTimeList;
            this.options.series[0].data = silverList;
            this.chartLine.setOption(this.options);
          }
        }
      });
      Toast.clear()
      this.goldList = goldList;
      this.goldTimeList = goldTimeList;
      this.silverList = silverList;
      this.silverTimeList = silverTimeList; //设置金银价格 时间列表

      this.chartLine = echarts.init(document.getElementById("chartLineBox"));
      this.option.xAxis.data = this.goldTimeList;
      this.option.series[0].data = this.goldList;
      this.chartLine.setOption(this.option);

      this.chartLine = echarts.init(
        document.getElementById("chartLineBoxSliver")
      );
      this.options.xAxis.data = this.silverTimeList;
      this.options.series[0].data = this.silverList;
      this.chartLine.setOption(this.options);
      // console.log("111");
      this.timer = setInterval(this.getGold, 3000);
      return;

      this.chartLine = echarts.init(document.getElementById("chartLineBox"));

      this.chartLine.setOption(this.option);
      this.chartLineSliver = echarts.init(
        document.getElementById("chartLineBoxSliver")
      );
      this.chartLineSliver.setOption(this.options);
    });
    // this.getGold()
  },
  beforeDestroy() {
    clearInterval(this.timer);
  },

  data() {
    return {
      gettime: "",
      goldList: [],
      silverList: [],
      goldTimeList: [],
      silverTimeList: [],
      // timeList:[],
      categoryData: [],
      currentIndex: 15,
      chartLine: "",
      chartLineSliver: "",
      option: {
        tooltip: {
          //设置tip提示
          trigger: "axis",
          formatter(params) {
            //  console.log(params);
            return `${params[0].name}<br/>Gold:${params[0].data}`;
          }
        },
        label: {
          show: true,
          position: "right",
          formatter: params => {
            // console.log("params",params);
          }
        },
        dataZoom: [
          {
            type: "inside" //详细配置可见echarts官网
          }
        ],
        grid: {
          x: 50
        },
        legend: {
          //设置区分（哪条线属于什么）
          data: ["gold"]
        },
        color: ["#FA6F53"], //设置区分（每条线是什么颜色，和 legend 一一对应）
        xAxis: {
          //设置x轴
          type: "category",
          boundaryGap: false, //坐标轴两边不留白
          data: [], //this.timeList,
          name: "t", //X轴 name
          //  axisLabel: {
          //   showMaxLabel: true
          // },
          nameTextStyle: {
            //坐标轴名称的文字样式
            color: "#FA6F53",
            fontSize: 12,
            padding: [0, 20, 0, 0]
          },
          axisLine: {
            //坐标轴轴线相关设置。
            lineStyle: {
              color: "#FA6F53"
            }
          }
        },
        yAxis: {
          name: "price",
          min: function(value) {
            return value.min;
          },
          nameTextStyle: {
            color: "#FA6F53",
            fontSize: 16,
            padding: [0, 0, 10, 0]
          },
          axisLine: {
            lineStyle: {
              color: "#FA6F53"
            }
          },
          type: "value"
        },
        series: [
          {
            name: "gold",
            data: [], //this.goldList,
            type: "line",
            lineStyle: {
              normal: {
                color: "#FA6F53"
                // padding: [0, 10, 0, 0]
              }
            },
            markPoint: {
              label: {
                show: true,
                position: "top",
                distance: 7,
                offset: [1, 1],
                formatter: "{b}: {c}"
              },
              color: "#FA6F53",
              symbol: "circle",
              symbolSize: 10,
              symbolOffset: [0, 0],
              data: [{ type: "max", name: "MaxPrice" }]
            }
          },
          {
            data: [],
            type: "line",
            label: {
              show: false
            },
            areaStyle: {
              color: "#fffbe5",
              opacity: 0.5
            }
          }
        ]
      },
      options: {
        tooltip: {
          //设置tip提示
          trigger: "axis",
          formatter(params) {
            //  console.log(params);
            return `${params[0].name}<br/>Silver:${params[0].data}`;
          }
        },
        grid: {
          x: 50
        },
        label: {
          show: true,
          position: "right",
          formatter: params => {
            // console.log("params",params);
          }
        },
        dataZoom: [
          {
            type: "inside" //详细配置可见echarts官网 折线图的放大缩小
          }
        ],
        legend: {
          //设置区分（哪条线属于什么）
          data: ["Silver"]
        },
        color: ["#3b86e8"], //设置区分（每条线是什么颜色，和 legend 一一对应）
        xAxis: {
          //设置x轴
          type: "category",
          boundaryGap: true, //坐标轴两边不留白
          data: [], //this.timeList,
          name: "t", //X轴 name
          nameTextStyle: {
            //坐标轴名称的文字样式
            color: "#3b86e8",
            fontSize: 12,
            padding: [0, 20, 0, 0]
          },
          axisLine: {
            //坐标轴轴线相关设置.
            lineStyle: {
              color: "#3b86e8"
            }
          }
        },
        yAxis: {
          name: "price",
          min: function(value) {
            return value.min;
          },
          nameTextStyle: {
            color: "#3b86e8",
            fontSize: 16,
            padding: [0, 0, 10, 0]
          },
          axisLine: {
            lineStyle: {
              color: "#3b86e8"
            }
          },
          type: "value"
        },
        series: [
          {
            name: "Silver",
            data: [], //this.goldList,
            type: "line",
            lineStyle: {
              normal: {
                color: "#3b86e8"
              }
            },
            markPoint: {
              label: {
                show: true,
                position: "top",
                distance: 7,
                offset: [1, 1],
                formatter: "{b}: {c}"
              },
              color: "#3b86e8",
              symbol: "circle",
              symbolSize: 10,
              symbolOffset: [0, 0],
              data: [{ type: "max", name: "MaxPrice" }]
            }
          },
          {
            data: [],
            type: "line",
            areaStyle: {
              color: "#bceef0",
              opacity: 0.7
            }
          }
        ]
      }
    };
  },
  //  mounted() {
  //   // this.setWrapHeight()
  //   // const { data } = await getCategory()
  //   // this.categoryData = data
  // },
  methods: {
    // getAllAug(){
    //    getAllAuAg().then(res=>{
    //      console.log("res",res);
    //    })
    // },
    getTime(T) {
      //获取当前时间并打印
      var _this = this;
      // 　　let yy = new Date().getFullYear();
      // 　　let mm = new Date().getMonth()+1;
      // 　　let dd = new Date().getDate();
      let hh = new Date(T).getHours();
      let mf =
        new Date(T).getMinutes() < 10
          ? "0" + new Date(T).getMinutes()
          : new Date(T).getMinutes();
      let ss =
        new Date(T).getSeconds() < 10
          ? "0" + new Date(T).getSeconds()
          : new Date(T).getSeconds();
      _this.gettime = hh + ":" + mf + ":" + ss;
      // console.log(_this.gettime);
    },
    getCurrentTime() {
      var date = new Date();
      let time1 = 60 * 60 * 1000 * 2.5;
      // let time = item.timestamp;
      let t = date.getTime();
      t = t - time1; //获取印度时间时间戳
      // console.log("t", t);
      this.getTime(t);
      //获取当前时间并打印
      // var _this = this;

      // // 　　let yy = new Date().getFullYear();
      // // 　　let mm = new Date().getMonth()+1;
      // // 　　let dd = new Date().getDate();
      // let hh = new Date().getHours();
      // let mf =
      //   new Date().getMinutes() < 10
      //     ? "0" + new Date().getMinutes()
      //     : new Date().getMinutes();
      // let ss =
      //   new Date().getSeconds() < 10
      //     ? "0" + new Date().getSeconds()
      //     : new Date().getSeconds();
      // _this.gettime = hh + ":" + mf + ":" + ss;
      // console.log(_this.gettime);
    },
    getGold() {
      getgold()
        .then(res => {
          // console.log(res);
          var date = new Date(res.data.timestamp);
          let time1 = 60 * 60 * 1000 * 2.5;
          // let time = item.timestamp;
          let t = date.getTime();
          t = t - time1; //获取印度时间时间戳
          this.getTime(t);
          if (
            res.data.au != undefined &&
            res.data.ag != undefined &&
            this.gettime != ""
          ) {
            // console.log(res.data.au, res.data.ag, this.gettime);
            this.goldList.push(res.data.au);
            this.silverList.push(res.data.ag);
            this.goldTimeList.push(this.gettime);
            this.silverTimeList.push(this.gettime);
            this.option.series[1].data = this.goldList;
            this.options.series[1].data = this.silverList;
          } else {
            return;
          }

          this.chartLine = echarts.init(
            document.getElementById("chartLineBox")
          );
          this.option.xAxis.data = this.goldTimeList;
          this.option.series[0].data = this.goldList;
          this.chartLine.setOption(this.option);

          this.chartLine = echarts.init(
            document.getElementById("chartLineBoxSliver")
          );
          this.options.xAxis.data = this.silverTimeList;
          this.options.series[0].data = this.silverList;
          this.chartLine.setOption(this.options);
          // console.log(res);
          // return;
          // console.log(this.goldList[this.goldList.length-1]);
          // if(this.goldList[this.goldList.length-1] ===res.rows.GOLD.last_price &&this.goldList.length>5)
          // return ;
          // this.goldList.push(res.rows.GOLD.last_price);
          // this.timeList.push(res.rows.GOLD.timestamp);
          //  this.chartLine = echarts.init(document.getElementById("chartLineBox"));
          //  this.option.xAxis.data = this.timeList;
          //  this.option.series[0].data = this.goldList;
          //    this.chartLine.setOption(this.option);
        })
        .catch(err => {
          // console.log("err",err);
          //  console.log(this.goldList[this.goldList.length-1]);
          // if(this.goldList.length>5 &&
          // this.silverList.length>5 &&
          // this.goldList[this.goldList.length-1] ===err.BM_HQ_AU &&
          // this.silverList[this.goldList.length-1] ===err.BM_HQ_Ag
          // )
          // return ;
        });
    },
    goHome() {
      this.$router.push({ path: "home" });
    },
    setWrapHeight() {
      // 设置视口高度
      let $screenHeight = document.documentElement.clientHeight;
      this.$refs.searchWrap.style.height = $screenHeight - 100 + "px";
    },
    selectMenu(index) {
      this.currentIndex = index;
    },
    selectProduct(item) {
      this.$router.push({ path: `product-list?categoryId=${item.categoryId}` });
    }
  }
};
</script>

<style lang="less" scoped>
@import "../common/style/mixin";
.categray {
  .category-header {
    background: #fff;
    position: fixed;
    left: 0;
    top: 0;
    .fj();
    .wh(100%, 50px);
    line-height: 50px;
    padding: 0 15px;
    box-sizing: border-box;
    font-size: 15px;
    color: #656771;
    z-index: 10000;
    &.active {
      background: @primary;
    }
    .icon-left {
      font-size: 25px;
      font-weight: bold;
    }
    .header-search {
      display: flex;
      width: 80%;
      height: 20px;
      line-height: 20px;
      margin: 10px 0;
      padding: 5px 0;
      color: #232326;
      background: #f7f7f7;
      border-radius: 20px;
      .nbSearch {
        padding: 0 10px 0 20px;
        font-size: 17px;
      }
      .search-title {
        font-size: 12px;
        color: #666;
      }
    }
    .icon-More {
      font-size: 20px;
    }
  }
}
.search-wrap {
  .fj();
  width: 100%;
  margin: 50px auto;
  // margin-top: 50px;
  background: #fff;
  border-top: 1px solid #999;
  display: flex;
  box-sizing: border-box;
  flex-direction: column;
  .line {
    padding-left: 12px;
    width: 90%;
    div {
      canvas {
        padding-left: 10px;
        margin: 10px auto;
      }
    }
  }
  .nav-side-wrapper {
    width: 28%;
    height: 100%;
    overflow: hidden;
    .nav-side {
      width: 100%;
      .boxSizing();
      background: #f8f8f8;
      li {
        width: 100%;
        height: 56px;
        text-align: center;
        line-height: 56px;
        font-size: 14px;
        &.active {
          color: @primary;
          background: #fff;
        }
      }
    }
  }
  .search-content {
    width: 72%;
    height: 100%;
    padding: 0 10px;
    background: #fff;
    .boxSizing();
    .swiper-container {
      width: 100%;
      .swiper-slide {
        width: 100%;
        .category-main-img {
          width: 100%;
        }
        .category-list {
          display: flex;
          flex-wrap: wrap;
          flex-shrink: 0;
          width: 100%;
          .catogory-title {
            width: 100%;
            font-size: 17px;
            font-weight: 500;
            padding: 20px 0;
          }
          .product-item {
            width: 33.3333%;
            margin-bottom: 10px;
            text-align: center;
            font-size: 15px;
            .product-img {
              .wh(30px, 30px);
            }
          }
        }
      }
    }
  }
}
.fade-out-enter-active,
.fade-out-leave-active {
  // transition: opacity 0.5s;
}

.fade-out-enter,
.fade-out-leave-to {
  // opacity: 0;
}
</style>
