import axios from 'axios'
import { Toast } from 'vant'
import router from '../router'
// axios.create({
//   baseURL:'http://45.40.194.29:28019'
// })
axios.defaults.baseURL =  'https://shuukgold.ga' //'http://112.213.127.123:28019' //'https://shuukgold.ga'  //    //http://45.40.194.29:28019
 //axios.defaults.baseURL  = process.env.NODE_ENV == 'development' ? 'http://192.168.1.103:28019/api/v1' : 'http://192.168.1.103:28019/api/v1' 
//http://localhost:28019/manage-api/v1/orders http://localhost:28019/api/v1 45.40.194.29
axios.defaults.withCredentials = true
axios.defaults.headers['X-Requested-With'] = 'XMLHttpRequest'
axios.defaults.headers['token'] = localStorage.getItem('token') || ''
axios.defaults.headers.post['Content-Type'] = 'application/json';
// axios.defaults.headers['proxy']=true;
axios.interceptors.request.use((config)=>{
  // console.log("config",config);
  // if(config.url=="/getAugPrice"){
    // if(config.headers['Content-Type']=="application/x-www-form-urlencoded"){
      // config.baseURL = '/payment'
      // console.log(config);
    // }
  // }
 
  return config
})
axios.interceptors.response.use(res => {
  if (typeof res.data !== 'object') {
    Toast.fail('服务端异常！')
    return Promise.reject(res)
  }
  if (res.data.resultCode != 200) {
    if (res.data.message) 
    // Toast.fail(res.data.message)
    if (res.data.resultCode == 416) {
      router.push({ path: '/login' })
    }
    return Promise.reject(res.data)
  }

  return res.data
})

export default axios
